.page
{
    border: solid 1px #bbb;
    position: relative;
    z-index: 90;
    margin: 5em 0 0 0;
    box-shadow: 0 0 1.5em #ccc;
}
    .page__left
    {
        width: 30%;
        float: left;
    }
    .page__right
    {
        width: 67%;
        float: right;
        padding: 2em 0;
    }